* {
  margin: 0;
  padding: 0;
}

input#targetWord {
  -webkit-text-security: disc;
}

.App {
  background-color: #92a8d1;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  height: 100vh;
}

h1 {
  padding: 40px;
}

.notification-container {
  background-color: red;
  border-radius: 5px;
  margin: 20px;
  transition: transform 0.3s ease-in-out;
}

.gameConfig_container p {
  margin-bottom: 20px;
}

.gameConfig_container p {
  margin-bottom: 20px;
}

.gameConfig_container button {
  margin-left: 5px;
}
.status-container p {
  margin: 20px;
}

.figure-container {
  fill: transparent;
  stroke: #fff;
  stroke-width: 4px;
  stroke-linecap: round;
  margin: 30px;
}

.result-container {
  margin: 30px;
}
.result-container button {
  cursor: pointer;
  background-color: #fff;
  color: #2980b9;
  border-radius: 5px;
  margin-top: 20px;
  padding: 12px 20px;
  font-size: 16px;
}

.result-container button:active {
  transform: scale(0.98);
}

.win {
  color: #4caf50;
}

.lost {
  color: #f44336;
}
